import { _decorator, Component, EventTouch, Input, input, Node, Vec3 } from "cc";
const { ccclass, property } = _decorator;

@ccclass("JoystickController")
export class JoystickController extends Component {
  @property(Node) player: Node = null;
  @property(Node) stick: Node = null;

  onLoad() {
    console.log(this.node.name);
    this.node.on(Node.EventType.TOUCH_START,this.onTouchStart,this)
    
  }

  onTouchStart(event : EventTouch) {
    console.log(event);
    
  }

}
